export class Bounds {
  constructor(left, top, width, height) {
    this.left = left;
    this.top = top;
    this.width = width;
    this.height = height;
  }

  get bottom() {
    const top = this.top,
          height = this.height;
    return top + height;
  }

  get right() {
    const left = this.left,
          width = this.width;
    return left + width;
  }

  get center() {
    const left = this.left,
          top = this.top,
          width = this.width,
          height = this.height,
          x = left + width * .5,
          y = top + height * .5;
    return {
      x,
      y
    };
  }

  get n() {
    const left = this.left,
          top = this.top,
          width = this.width,
          x = left + width * .5,
          y = top;
    return {
      x,
      y
    };
  }

  get ne() {
    const left = this.left,
          top = this.top,
          width = this.width,
          x = left + width,
          y = top;
    return {
      x,
      y
    };
  }

  get e() {
    const left = this.left,
          top = this.top,
          width = this.width,
          height = this.height,
          x = left + width,
          y = top + height * .5;
    return {
      x,
      y
    };
  }

  get se() {
    const left = this.left,
          top = this.top,
          width = this.width,
          height = this.height,
          x = left + width,
          y = top + height;
    return {
      x,
      y
    };
  }

  get s() {
    const left = this.left,
          top = this.top,
          width = this.width,
          height = this.height,
          x = left + width * .5,
          y = top + height;
    return {
      x,
      y
    };
  }

  get sw() {
    const left = this.left,
          top = this.top,
          height = this.height,
          x = left,
          y = top + height;
    return {
      x,
      y
    };
  }

  get w() {
    const left = this.left,
          top = this.top,
          height = this.height,
          x = left,
          y = top + height * .5;
    return {
      x,
      y
    };
  }

  get nw() {
    const left = this.left,
          top = this.top,
          x = left,
          y = top;
    return {
      x,
      y
    };
  }

  hitTest({
    x,
    y
  }) {
    const top = this.top,
          right = this.right,
          bottom = this.bottom,
          left = this.left;
    return x >= left && x <= right && y >= top && y <= bottom;
  }

}